.App {
    background-color: rgba(227, 234, 243, 0.9); /* 设置背景颜色为柔和的灰蓝色并增加透明度 */
    box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.15); /* 添加更深的内阴影以增加深度 */
    border: 1px solid rgba(163, 177, 198, 0.9); /* 添加一个与背景色调相近但更深的边框并增加透明度 */
}

.HaiNougat-header{
    margin-bottom: 6px; /* 设置下边距为20像素 */
}

.HaiNougat-branding {
    display: flex;         /* 启用Flexbox */
    align-items: center;   /* 垂直居中所有子元素 */
  }
  
.HaiNougat-logo {
    margin-left: 10px;    /* 或者你需要的间隔 */
    height: 40px; /* 你可以添加更多样式来控制logo的大小等 */
}

.HaiNougat-h {
    font-size: 30px; /* 设置字体大小 */
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
    padding: 10px;
    margin-left: 12px;
    margin-top: 14px;
}

.HaiNougat-p {
    font-family: "Open Sans", "Helvetica", "Arial", serif; /* 设置字体 */
    font-size: 16px; /* 设置字体大小 */
    line-height: 1.5; /* 设置行高 */
    margin-right: 20px; /* 设置右边距为20像素 */
    margin-left: 50px; /* 设置左边距为40像素 */
    margin-bottom: 5px; /* 设置下边距为20像素 */
    color: black;
    font-weight: 500;
}

.HaiNougat-p::before {
    content: url('./tips.svg'); /* 设置图标的路径 */
    margin-right: 4px; /* 设置图标与文本之间的右边距 */
    vertical-align: middle; /* 使图标与文本垂直居中对齐 */
}
