.markdownEditContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  border-top: 4px solid  #767575; /* 添加一个上边界框 */
}

.mmdContainer-main {
  display: flex;
  flex: 1;
}

.mmdedit-container {
  flex: 1;
  display: flex;
}

.mmdshow-container{
  flex: 1;
  display: flex;
}

.mmdedit {
  flex: 1;
  border: 0;
  border-right: 1px solid #ccc; /* 右边框改为浅灰色 */
  background-color: rgb(25, 25, 25);
  color: white;
  padding: 20px;
  line-height: 1.7;
  resize: none;
  outline: none;
  box-sizing: border-box;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  overflow: auto;
}

.separator {
  width: 12px; /* 设置间隔宽度 */
  background-color: #cccaca; /* 设置浅灰色背景色 */
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); /* 添加阴影效果 */
}

.mmdshow {
  flex: 1;
  padding: 20px;
  overflow: auto;
  background-color: white;
  box-sizing: border-box;
  border-left: 1px solid #ccc; /* 左边框改为浅灰色 */
}